 * @Description:查看看更多数据标签
 * @Author: zhangshujiang
 * @Date: 2019-07-23 16:24:57
 * @LastEditors: zhangshujiang
 * @LastEditTime: 2019-08-22 16:52:30
 -->
<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <common-list
        ref="commonlist"
        class="common-list"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        @returnData="getPrizeLog($event)"
      >
        <div v-for="(item,index) of homedata" :key="index">
          <mouldsList v-if="item.type===2" :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />
          <mouldList v-if="item.type===1" :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />
        </div>
      </common-list>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import mouldsList from '@/components/list/mouldsList.vue'
import mouldList from '@/components/list/mouldList.vue'
import { mapState } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'
import commonList from '@/components/list/commonList.vue'
export default {
  name: 'DbsxSjbqMore',
  components: {
    // [ScrollView.name]: ScrollView,
    // [ScrollViewRefresh.name]: ScrollViewRefresh,
    // [ScrollViewMore.name]: ScrollViewMore,
    headerBar,
    mouldsList,
    commonList,
    mouldList
  },
  mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      headerData: {
        title: '更多信息',
        leftUrl: 'back',
        leftText: ' '
      },
      homedata: { data: [] },
      morexx: [],
      ind: '', // 点击事件
      pageStyles: 0, // 页面状态展示
      pageText: '', // 页面提示信息
      showMore: false,
      pageno: 1
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.user.userInfo
    })
  },

  created() {
    // 登录页面判断
    // if (!this.isLogin) {
    this.ind = ''
    this.morexx = this.$route.params.morexx
    this.getdbsxlist()
    // }
  },
  methods: {
    /** 组件中返回的数据结构
     */
    getPrizeLog(xxList) {
      this.homedata = xxList
      console.log('xxList==================================================', this.homedata)
    },
    getdbsxlist() {
      this.list.postUrl = 'jbrdbsx/getMoreSJBQData'
      this.list.reqData = {
        url: this.morexx[0],
        pageno: this.pageno,
        pagerow: 5
      }
      // this.list.noDataText = '暂无更多待办事项'
    },
    changeBgc: function(index) {
      this.ind = index
    },
    getmore() {
      this.$store.dispatch('jbrdbsx/getMoreSJBQData', {
        url: this.morexx[0],
        pagerow: 5
      }).then(res => {
        this.homedata = res
      }).catch(error => {
        this.$toast(error)
      })
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>

<style lang="css" scoped>
.xy-scroll-view{height: 100vh; background-color: #ffffff}
</style>
